@Composable
fun WritingGrant(
    title: String,
    medium: String,
    descr: String,
    paddingTop: Double,
    marginTop: Double
) {
    Column(
        Modifier
            .padding(top = paddingTop.cssRem)
            .margin(top = marginTop.cssRem)
    ) {
        SpanText(
            modifier = Modifier
                .fontSize(1.25.cssRem)
                .lineHeight(1.75.cssRem)
                .color(Res.Theme.FONT_DARK.color)
                .fontWeight(500)
                .fontFamily(Res.String.INTERREG),
            text = title
        )
