@Composable
fun HeroSection(ctx: PageContext) {
    Box(
        modifier = Modifier
            .fillMaxWidth()
            .backgroundImage(
                url = url(Res.Image.BACKGROUND)
            )
            .backgroundSize(BackgroundSize.Cover)
            .backgroundPosition(BackgroundPosition.of(CSSPosition.Center))
            .backgroundPosition(BackgroundPosition.of(CSSPosition.Right))
            .padding(top = 10.cssRem, bottom = 8.cssRem),
    ) {
        Column(
            modifier = ThemeMaxWidth.toModifier()
                .fillMaxWidth()
                .padding(leftRight = 1.5.cssRem)
                .margin(leftRight = autoLength)
        ) {
            Column(
                modifier = Modifier
                    .maxWidth(42.cssRem)
            ) {
                SpanText(
                    modifier = FontSizeHeroTitle.toModifier()
                        .fontWeight(700)
                        .color(Res.Theme.FONT_DARK_TITLE.color)
                        .fontFamily(Res.String.PLAYFAIRDISPLAY_BOLD)
                        .margin(bottom = 1.5.cssRem),
                    text = if (ctx.route.path == "/de/") Lang.StringDE.HERO_TITLE
                    else Lang.StringEN.HERO_TITLE
                )
                SpanText(
                    modifier = FontSizeHero.toModifier()
                        .margin(bottom = 2.cssRem)
                        .color(Res.Theme.FONT_DARK_HERO_DESC.color)
                        .fontFamily(Res.String.INTERREG),
                    text = if (ctx.route.path == "/de/") Lang.StringDE.HERO_DESC
                    else Lang.StringEN.HERO_DESC
                )
                Row(
                    modifier = HeroButtonStyle.toModifier()
                        .fillMaxWidth()
                        .gap(1.cssRem)
                ) {
                    Button(
                        modifier = ButtonStyleMain.toModifier()
                            .padding(leftRight = 2.cssRem),
                        size = ButtonSize.LG,
                        onClick = { window.location.href = Res.String.MY_EMAIL }
                    ) {
                        Image(
                            Res.Icon.EMAIL,
                            modifier = Modifier.margin(right = 0.75.cssRem)
                        )
                        SpanText(
                            modifier = Modifier
                                .fontSize(1.cssRem)
                                .color(Res.Theme.LIGHT.color),
                            text = if (ctx.route.path == "/de/") Lang.StringDE.HERO_BUTTON_1
                            else Lang.StringEN.HERO_BUTTON_1
                        )
                    }
                    Button(
                        modifier = ButtonStyleSec.toModifier()
                            .padding(leftRight = 2.cssRem),
                        size = ButtonSize.LG,
                        onClick = { window.location.href = "en" }
                    ) {
                        SpanText(
                            modifier = Modifier
                                .fontSize(1.cssRem)
                                .color(Res.Theme.FONT_DARK_TITLE.color),
                            text = if (ctx.route.path == "/de/") Lang.StringDE.HERO_BUTTON_2
                            else Lang.StringEN.HERO_BUTTON_2
                        )
                    }
                }
            }
        }
    }
}