package org.example.styleswebsite.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.autoLength
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.alignItems
import com.varabyte.kobweb.compose.ui.modifiers.backgroundColor
import com.varabyte.kobweb.compose.ui.modifiers.borderRadius
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.maxWidth
import com.varabyte.kobweb.compose.ui.modifiers.padding
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.PageContext
import com.varabyte.kobweb.silk.style.toModifier
import org.example.styleswebsite.components.widgets.SectionTitle
import org.example.styleswebsite.components.widgets.WritingGrant
import org.example.styleswebsite.styles.CardShadowStyle
import org.example.styleswebsite.styles.ThemeMaxWidth
import org.example.styleswebsite.util.Lang
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.AlignItems
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Section

@Composable
fun WritingGrants( ctx: PageContext) {
    Section(
        Modifier
            .padding(topBottom = 5.cssRem)
            .backgroundColor(Res.Theme.LIGHT_D.color)
            .toAttrs()
    ) {
        Column(
            ThemeMaxWidth.toModifier()
                .margin(leftRight = autoLength)
                .padding(leftRight = 1.5.cssRem)
                .alignItems(AlignItems.Center)
                .fillMaxWidth()
        ) {
            SectionTitle(
                title = if (ctx.route.path == "/de/") Lang.StringDE.GRANTS
                else Lang.StringEN.GRANTS,
                descr = if (ctx.route.path == "/de/") Lang.StringDE.GRANTS_DESCR
                else Lang.StringEN.GRANTS_DESCR,
            )
            Box(
                CardShadowStyle.toModifier()
                    .padding(all = 2.cssRem)
                    .backgroundColor(Res.Theme.LIGHT.color)
                    .borderRadius(16.px)
                    .maxWidth(48.cssRem)
            ) {
                Column(
                    Modifier
                        .margin(all = 0.px)
                        .padding(all = 0.px)
                ) {
                    WritingGrant(
                        if (ctx.route.path == "/de/") Lang.StringDE.GRANTS_1
                        else Lang.StringEN.GRANTS_1,
                        "",
                        "",
                        0.0,
                        0.0
                    )
                    WritingGrant(
                        if (ctx.route.path == "/de/") Lang.StringDE.GRANTS_2
                        else Lang.StringEN.GRANTS_2,
                        "",
                        "",
                        1.5,
                        1.5
                    )
                    WritingGrant(
                        if (ctx.route.path == "/de/") Lang.StringDE.GRANTS_3
                        else Lang.StringEN.GRANTS_3,
                        "",
                        "",
                        1.5,
                        1.5
                    )
                    WritingGrant(
                        if (ctx.route.path == "/de/") Lang.StringDE.GRANTS_4
                        else Lang.StringEN.GRANTS_4,
                        "",
                        "",
                        1.5,
                        1.5
                    )
                }
            }
        }
    }
}