package org.example.styleswebsite.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.Overflow
import com.varabyte.kobweb.compose.css.ScrollBehavior
import com.varabyte.kobweb.compose.css.autoLength
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.PageContext
import com.varabyte.kobweb.silk.style.toModifier
import org.example.styleswebsite.components.widgets.Presentation
import org.example.styleswebsite.components.widgets.SectionTitle
import org.example.styleswebsite.styles.CardShadowStyle
import org.example.styleswebsite.styles.ThemeMaxWidth
import org.example.styleswebsite.util.Lang
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.AlignItems
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Section

@Composable
fun Presentations(ctx: PageContext) {
    Section(
        Modifier
            .padding(topBottom = 5.cssRem)
            .backgroundColor(Res.Theme.LIGHT.color)
            .toAttrs()
    ) {
        Column(
            ThemeMaxWidth.toModifier()
                .margin(leftRight = autoLength)
                .padding(leftRight = 1.5.cssRem)
                .alignItems(AlignItems.Center)
                .fillMaxWidth()
        ) {
            SectionTitle(
                title = if (ctx.route.path == "/de/") Lang.StringDE.PRESENTATIONS
                else Lang.StringEN.PRESENTATIONS,
                descr = if (ctx.route.path == "/de/") Lang.StringDE.PRES_DESCR
                else Lang.StringEN.PRES_DESCR,
            )
            Box(
                CardShadowStyle.toModifier()
                    .padding(all = 2.cssRem)
                    .backgroundColor(Res.Theme.LIGHT.color)
                    .borderRadius(16.px)
                    .maxWidth(56.cssRem)
                    .overflow(Overflow.Hidden)
            ) {
                Column(
                    Modifier
                        .fillMaxWidth()
                        .height(620.px)
                        .margin(all = 0.px)
                        .padding(all = 0.px)
                        .overflow(Overflow.of(Overflow.Auto, Overflow.Scroll))
                ) {
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_1_TITLE else Lang.StringEN.PRES_1_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_1_MED else Lang.StringEN.PRES_1_MED,
                        "",
                        0.0,
                        0.0
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_2_TITLE else Lang.StringEN.PRES_2_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_2_MED else Lang.StringEN.PRES_2_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_3_TITLE else Lang.StringEN.PRES_3_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_3_MED else Lang.StringEN.PRES_3_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_4_TITLE else Lang.StringEN.PRES_4_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_4_MED else Lang.StringEN.PRES_4_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_5_TITLE else Lang.StringEN.PRES_5_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_5_MED else Lang.StringEN.PRES_5_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_6_TITLE else Lang.StringEN.PRES_6_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_6_MED else Lang.StringEN.PRES_6_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_7_TITLE else Lang.StringEN.PRES_7_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_7_MED else Lang.StringEN.PRES_7_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Presentation(
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_8_TITLE else Lang.StringEN.PRES_8_TITLE,
                        if (ctx.route.path == "/de/") Lang.StringDE.PRES_8_MED else Lang.StringEN.PRES_8_MED,
                        "",
                        1.5,
                        1.5
                    )
                }
            }
        }
    }
}