package org.example.styleswebsite.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fontFamily
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.height
import com.varabyte.kobweb.compose.ui.modifiers.lineHeight
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.width
import com.varabyte.kobweb.silk.components.icons.mdi.MdiCheck
import com.varabyte.kobweb.silk.components.text.SpanText
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px

@Composable
fun ServiceList(
    textList: List<String>
) {
    for (text in textList) {
        Row(
            modifier = Modifier
                .margin(top = 0.5.cssRem)
        ) {
            MdiCheck(
                modifier = Modifier
                    .width(1.cssRem)
                    .height(1.cssRem)
                    .fontSize(16.px)
            )
            SpanText(
                modifier = Modifier
                    .color(Res.Theme.FONT_DARK_DESC.color)
                    .fontFamily(Res.String.INTERREG)
                    .fontSize(1.cssRem)
                    .lineHeight(1.5.cssRem)
                    .margin(left = 0.5.cssRem),
                text = text
            )
        }
    }
}
