import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import org.example.styleswebsite.pages.HomePage as _org_example_styleswebsite_pages_HomePage
import org.example.styleswebsite.pages.de.Index as _org_example_styleswebsite_pages_de_Index
import org.example.styleswebsite.pages.en.Index as _org_example_styleswebsite_pages_en_Index

public fun main() {
    AppGlobals.initialize(mapOf("title" to "StylesWebsite"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register(
            "/",
        ) { pageCtx -> 
            _org_example_styleswebsite_pages_HomePage(pageCtx)
        }
        ctx.router.register(
            "/de/",
        ) { pageCtx -> 
            _org_example_styleswebsite_pages_de_Index(pageCtx)
        }
        ctx.router.register(
            "/en/",
        ) { pageCtx -> 
            _org_example_styleswebsite_pages_en_Index(pageCtx)
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("button-style-main", org.example.styleswebsite.styles.ButtonStyleMain)
        ctx.theme.registerStyle("button-style-sec", org.example.styleswebsite.styles.ButtonStyleSec)
        ctx.theme.registerStyle("theme-icon", org.example.styleswebsite.styles.ThemeIconStyle)
        ctx.theme.registerStyle("theme-max-width", org.example.styleswebsite.styles.ThemeMaxWidth)
        ctx.theme.registerStyle("hero-button", org.example.styleswebsite.styles.HeroButtonStyle)
        ctx.theme.registerStyle("font-size-hero-title", org.example.styleswebsite.styles.FontSizeHeroTitle)
        ctx.theme.registerStyle("font-size-hero", org.example.styleswebsite.styles.FontSizeHero)
        ctx.theme.registerStyle("card-grid", org.example.styleswebsite.styles.CardGridStyle)
        ctx.theme.registerStyle("card-shadow", org.example.styleswebsite.styles.CardShadowStyle)
        ctx.theme.registerStyle("about", org.example.styleswebsite.styles.AboutStyle)
        ctx.theme.registerStyle("portrait", org.example.styleswebsite.styles.PortraitStyle)
        ctx.theme.registerStyle("about-text", org.example.styleswebsite.styles.AboutTextStyle)
        org.example.styleswebsite.initStyles(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        org.example.styleswebsite.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
