package org.example.styleswebsite.styles

import com.varabyte.kobweb.compose.css.Transition
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.background
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.transition
import com.varabyte.kobweb.silk.style.CssStyle
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.selectors.hover
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px

val ThemeIconStyle = CssStyle {
    base {
        Modifier
            .background(
                Res.Theme.LIGHT.color
            )
            .transition(
                Transition.of("background", duration = 300.ms)
            )
    }
    hover {
        Modifier
            .background(
                Res.Theme.SOCIAL_ICON_BACKGROUND_LIGHT.color
            )
    }
}