import org.jetbrains.compose.web.css.px

@Composable
fun ServiceCard(
    ctx: PageContext,
    serviceList: List<List<String>>,
    title: String,
    descr: String
) {
    SpanText(
        text = title,
        modifier = Modifier
            .fontSize(1.5.cssRem)
            .lineHeight(2.cssRem)
            .fontFamily(Res.String.PLAYFAIRDISPLAY_SEMIBOLD)
            .margin(bottom = 1.cssRem)
            .color(Res.Theme.FONT_DARK.color)
    )
    SpanText(
        text = descr,
        modifier = Modifier
            .fontSize(1.cssRem)
            .lineHeight(1.5.cssRem)
            .fontFamily(Res.String.INTERREG)
            .margin(bottom = 1.5.cssRem)
            .color(Res.Theme.FONT_DARK_DESC.color)
    )
    Column(
        modifier = Modifier
            .margin(bottom = 1.5.cssRem)
    ) {
        if (ctx.route.path == "/de/") ServiceList(serviceList[0])
        else ServiceList(serviceList[1])
    }
}