@Composable
fun HeaderSection(ctx: PageContext) {
    Header(
        Modifier
            .fillMaxWidth()
            .position(Position.Fixed)
            .zIndex(50)
            .backgroundColor(Res.Theme.LIGHT.color)
            .toAttrs()
    ) {
        Row(
            modifier = ThemeMaxWidth.toModifier()
                .margin(leftRight = autoLength)
                .fillMaxWidth()
                .padding(topBottom = 11.px, leftRight = 1.5.cssRem),
            verticalAlignment = Alignment.CenterVertically,
            horizontalArrangement = Arrangement.SpaceBetween
        ) {
            SpanText(
                modifier = Modifier
                    .fontSize(20.px)
                    .color(Colors.Black)
                    .fontFamily(Res.String.PLAYFAIRDISPLAY_BOLD),
                text = "Dr. Styles Sass"
            )
            Nav(
                attrs = Modifier
                    .displayIfAtLeast(Breakpoint.MD)
                    .alignItems(AlignItems.Center)
                    .toAttrs()
            ) {
                Link(
                    "#Services",
                    if (ctx.route.path == "/de/") Lang.StringDE.SERVICES
                    else Lang.StringEN.SERVICES,
                    Modifier
                        .fontSize(1.cssRem)
                        .color(Res.Theme.FONT_DARK.color)
                        .textDecorationLine(TextDecorationLine.None)
                        .fontFamily(Res.String.INTERREG)
                )
                Link(
                    "#Portfolio",
                    if (ctx.route.path == "/de/") Lang.StringDE.PORTFOLIO
                    else Lang.StringEN.PORTFOLIO,
                    Modifier
                        .margin(left = 2.cssRem)
                        .fontSize(1.cssRem)
                        .color(Res.Theme.FONT_DARK.color)
                        .textDecorationLine(TextDecorationLine.None)
                        .fontFamily(Res.String.INTERREG)
                )
                Link(
                    "#Publications",
                    if (ctx.route.path == "/de/") Lang.StringDE.PUBLICATIONS
                    else Lang.StringEN.PUBLICATIONS,
                    Modifier
                        .margin(left = 2.cssRem)
                        .fontSize(1.cssRem)
                        .color(Res.Theme.FONT_DARK.color)
                        .textDecorationLine(TextDecorationLine.None)
                        .fontFamily(Res.String.INTERREG)
                )
                Link(
                    "#About",
                    if (ctx.route.path == "/de/") Lang.StringDE.ABOUT
                    else Lang.StringEN.ABOUT,
                    Modifier
                        .margin(left = 2.cssRem)
                        .fontSize(1.cssRem)
                        .color(Res.Theme.FONT_DARK.color)
                        .textDecorationLine(TextDecorationLine.None)
                        .fontFamily(Res.String.INTERREG)
                )
                Link(
                    "#Contact",
                    if (ctx.route.path == "/de/") Lang.StringDE.CONTACT
                    else Lang.StringEN.CONTACT,
                    Modifier
                        .margin(left = 2.cssRem)
                        .fontSize(1.cssRem)
                        .color(Res.Theme.FONT_DARK.color)
                        .textDecorationLine(TextDecorationLine.None)
                        .fontFamily(Res.String.INTERREG)
                )
            }
            LangSwitchButton(
                ctx,
                onClick = {
                    if (ctx.route.path == "/en/" ) window.location.href = "/de"
                    else window.location.href = "/en"
                }
            )
        }
    }
}