package org.example.styleswebsite.components

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.core.PageContext
import com.varabyte.kobweb.silk.style.toModifier
import com.varabyte.kobweb.silk.theme.colors.ColorMode
import org.example.styleswebsite.components.widgets.IconButton
import org.example.styleswebsite.styles.ThemeIconStyle
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.px

@Composable
fun LangSwitchButton(
    ctx: PageContext,
    onClick: () -> Unit
) {
    Column(
        modifier = Modifier
            .styleModifier {
                property("pointer-events", "none")
            },
    ) {
        IconButton(
            modifier = ThemeIconStyle.toModifier()
                .styleModifier {
                    property("pointer-events", "auto")
                },
            icon = if (ctx.route.path == "/de/") Res.Icon.USA else Res.Icon.GERMANY,
            iconSize = Res.Dimens.ICON_SIZE.px,
            onClick = onClick
        )
    }
}