package org.example.styleswebsite.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.TextAlign
import com.varabyte.kobweb.compose.css.autoLength
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.fontFamily
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.fontWeight
import com.varabyte.kobweb.compose.ui.modifiers.lineHeight
import com.varabyte.kobweb.compose.ui.modifiers.margin
import com.varabyte.kobweb.compose.ui.modifiers.maxWidth
import com.varabyte.kobweb.compose.ui.modifiers.textAlign
import com.varabyte.kobweb.silk.components.text.SpanText
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.cssRem

@Composable
fun SectionTitle(
    title: String,
    descr: String
) {
    Column(
        modifier = Modifier
            .fillMaxWidth()
            .textAlign(TextAlign.Center)
            .margin(bottom = 4.cssRem),
        verticalArrangement = Arrangement.Top,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        SpanText(
            modifier = Modifier
                .fontSize(2.25.cssRem)
                .lineHeight(2.5.cssRem)
                .fontWeight(700)
                .color(Res.Theme.FONT_DARK_TITLE.color)
                .margin(bottom = 1.cssRem)
                .fontFamily(Res.String.PLAYFAIRDISPLAY_SEMIBOLD),
            text = title
        )
        SpanText(
            modifier = Modifier
                .maxWidth(42.cssRem)
                .fontSize(1.cssRem)
                .lineHeight(1.5.cssRem)
                .margin(leftRight = autoLength)
                .color(Res.Theme.FONT_DARK_DESC.color)
                .fontFamily(Res.String.INTERREG),
            text = descr
        )
    }
}
