package org.example.styleswebsite.styles

import com.varabyte.kobweb.compose.css.Transition
import com.varabyte.kobweb.compose.css.TransitionProperty
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.backgroundColor
import com.varabyte.kobweb.compose.ui.modifiers.color
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.opacity
import com.varabyte.kobweb.compose.ui.modifiers.transition
import com.varabyte.kobweb.silk.style.CssStyle
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.selectors.hover
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.percent

val ButtonStyleMain = CssStyle {
    base {
        Modifier
            .backgroundColor(Res.Theme.FONT_DARK_TITLE.color)
            .transition(Transition.of(TransitionProperty.All, duration = 300.ms))
    }
    hover {
        Modifier
            .opacity(90.percent)
    }
    (Breakpoint.ZERO until Breakpoint.MD) {
        Modifier
            .fillMaxWidth()
    }
}
val ButtonStyleSec = CssStyle {
    base {
        Modifier
            .backgroundColor(Res.Theme.LIGHT.color)
            .transition(Transition.of(TransitionProperty.All, duration = 300.ms))
    }
    hover {
        Modifier
            .opacity(90.percent)
    }
    (Breakpoint.ZERO until Breakpoint.MD) {
        Modifier
            .fillMaxWidth()
    }
}