package org.example.styleswebsite.components.widgets

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.AlignItems
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.core.PageContext
import com.varabyte.kobweb.silk.components.icons.mdi.MdiBorderColor
import com.varabyte.kobweb.silk.components.icons.mdi.MdiTranslate
import com.varabyte.kobweb.silk.components.layout.SimpleGrid
import com.varabyte.kobweb.silk.components.layout.numColumns
import com.varabyte.kobweb.silk.style.toModifier
import org.example.styleswebsite.styles.CardGridStyle
import org.example.styleswebsite.styles.CardShadowStyle
import org.example.styleswebsite.util.Lang
import org.jetbrains.compose.web.css.JustifyContent
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px

@Composable
fun ServiceGrid(ctx: PageContext) {
    val textListEditDE = listOf(Lang.StringDE.SERVICE_CARD_EDIT_ITEM1, Lang.StringDE.SERVICE_CARD_EDIT_ITEM2, Lang.StringDE.SERVICE_CARD_EDIT_ITEM3, Lang.StringDE.SERVICE_CARD_EDIT_ITEM4)
    val textListEditEN = listOf(Lang.StringEN.SERVICE_CARD_EDIT_ITEM1, Lang.StringEN.SERVICE_CARD_EDIT_ITEM2, Lang.StringEN.SERVICE_CARD_EDIT_ITEM3, Lang.StringEN.SERVICE_CARD_EDIT_ITEM4)
    val textListTransDE = listOf(Lang.StringDE.SERVICE_CARD_TRANS_ITEM1, Lang.StringDE.SERVICE_CARD_TRANS_ITEM2, Lang.StringDE.SERVICE_CARD_TRANS_ITEM3, Lang.StringDE.SERVICE_CARD_TRANS_ITEM4)
    val textListTransEN = listOf(Lang.StringEN.SERVICE_CARD_TRANS_ITEM1, Lang.StringEN.SERVICE_CARD_TRANS_ITEM2, Lang.StringEN.SERVICE_CARD_TRANS_ITEM3, Lang.StringEN.SERVICE_CARD_TRANS_ITEM4)
    SimpleGrid(
        numColumns = numColumns(base = 1, md = 2),
        modifier = CardGridStyle.toModifier()
            .gap(2.cssRem)
    ) {
        Column(
            modifier = CardShadowStyle.toModifier()
                .padding(all = 2.cssRem)
                .backgroundColor(Colors.White)
                .borderRadius(16.px)
        ) {
            Row(
                modifier = Modifier
                    .alignItems(AlignItems.Center)
                    .justifyContent(JustifyContent.Center)
                    .borderRadius(9999.px)
                    .backgroundColor(Color.rgba(19,22,37,25))
                    .width(4.cssRem)
                    .height(4.cssRem)
                    .margin(bottom = 1.5.cssRem)
            ) {
                MdiBorderColor(Modifier
                    .color(Color.rgb(19,12,37))
                    .fontSize(1.5.cssRem)
                    .lineHeight(2.cssRem)
                )
            }
            ServiceCard(ctx, listOf(textListEditDE, textListEditEN),
                if (ctx.route.path == "/de/") Lang.StringDE.SERVICE_CARD_EDIT_TITLE
                else Lang.StringEN.SERVICE_CARD_EDIT_TITLE,
                if (ctx.route.path == "/de/") Lang.StringDE.SERVICE_CARD_EDIT_TEXT
                else Lang.StringEN.SERVICE_CARD_EDIT_TEXT
            )
        }
        Column(
            modifier = CardShadowStyle.toModifier()
                .padding(all = 2.cssRem)
                .backgroundColor(Colors.White)
                .borderRadius(16.px)
        ) {
            Row(
                modifier = Modifier
                    .alignItems(AlignItems.Center)
                    .justifyContent(JustifyContent.Center)
                    .borderRadius(9999.px)
                    .backgroundColor(Color.rgba(19,22,37,25))
                    .width(4.cssRem)
                    .height(4.cssRem)
                    .margin(bottom = 1.5.cssRem)
            ) {
                MdiTranslate(Modifier
                    .color(Color.rgb(19,12,37))
                    .fontSize(1.5.cssRem)
                    .lineHeight(2.cssRem)
                )
            }
            ServiceCard(ctx, listOf(textListTransDE, textListTransEN),
                if (ctx.route.path == "/de/") Lang.StringDE.SERVICE_CARD_TRANS_TITLE
                else Lang.StringEN.SERVICE_CARD_TRANS_TITLE,
                if (ctx.route.path == "/de/") Lang.StringDE.SERVICE_CARD_TRANS_TEXT
                else Lang.StringEN.SERVICE_CARD_TRANS_TEXT
            )
        }
    }
}
