package org.example.styleswebsite.styles

import com.varabyte.kobweb.compose.css.BoxShadow
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.boxShadow
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxWidth
import com.varabyte.kobweb.compose.ui.modifiers.flexDirection
import com.varabyte.kobweb.compose.ui.modifiers.fontSize
import com.varabyte.kobweb.compose.ui.modifiers.lineHeight
import com.varabyte.kobweb.compose.ui.modifiers.maxWidth
import com.varabyte.kobweb.compose.ui.modifiers.width
import com.varabyte.kobweb.silk.style.CssStyle
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.style.selectors.hover
import com.varabyte.kobweb.silk.theme.breakpoint.toPx
import org.jetbrains.compose.web.css.FlexDirection
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.rgba

val ThemeMaxWidth = CssStyle {
    (Breakpoint.SM until Breakpoint.MD) { Modifier.maxWidth(42.cssRem) }
    (Breakpoint.MD until Breakpoint.LG) { Modifier.maxWidth(Breakpoint.MD.toPx()) }
    (Breakpoint.LG until Breakpoint.XL) { Modifier.maxWidth(Breakpoint.LG.toPx()) }
    Breakpoint.XL { Modifier.maxWidth(Breakpoint.XL.toPx()) }
}

val HeroButtonStyle = CssStyle {
    base { Modifier.flexDirection(FlexDirection.Row) }
    (Breakpoint.ZERO until Breakpoint.MD) { Modifier.flexDirection((FlexDirection.Column)) }
}

val FontSizeHeroTitle = CssStyle {
    (Breakpoint.ZERO until Breakpoint.MD) { Modifier.fontSize(2.25.cssRem).lineHeight(1.25) }
    (Breakpoint.MD until Breakpoint.LG) { Modifier.fontSize(3.cssRem).lineHeight(1) }
    Breakpoint.LG { Modifier.fontSize(3.75.cssRem).lineHeight(1) }
}

val FontSizeHero = CssStyle {
    base { Modifier.fontSize(1.25.cssRem).lineHeight(1.75.cssRem) }
    (Breakpoint.ZERO until Breakpoint.MD) { Modifier.fontSize(1.125.cssRem).lineHeight(1.625.cssRem) }
}

val CardGridStyle = CssStyle {
    base { Modifier.fillMaxWidth() }
    (Breakpoint.MD until Breakpoint.LG) { Modifier.fillMaxWidth(48.cssRem) }
    Breakpoint.LG { Modifier.fillMaxWidth(62.cssRem) }
}

val CardShadowStyle = CssStyle {
    base { Modifier
        .boxShadow(BoxShadow.of(0.px,4.px,6.px, (-1).px, rgba(0,0,0,0.1)))
    }
    hover { Modifier
        .boxShadow(BoxShadow.of(0.px,10.px,15.px, (-3).px, rgba(0,0,0,0.1)))
    }
}
val AboutStyle = CssStyle {
    Breakpoint.MD { Modifier.flexDirection(FlexDirection.Row) }
}
val PortraitStyle = CssStyle {
    Breakpoint.MD { Modifier.width((100/3).percent) }
}
val AboutTextStyle = CssStyle {
    Breakpoint.MD { Modifier.width((200/3).percent) }
}
