package org.example.styleswebsite.components.sections

import androidx.compose.runtime.Composable
import com.varabyte.kobweb.compose.css.BackgroundAttachment
import com.varabyte.kobweb.compose.css.BackgroundPosition
import com.varabyte.kobweb.compose.css.BackgroundSize
import com.varabyte.kobweb.compose.css.CSSPosition
import com.varabyte.kobweb.compose.css.autoLength
import com.varabyte.kobweb.compose.css.functions.url
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.PageContext
import com.varabyte.kobweb.silk.style.toModifier
import org.example.styleswebsite.components.widgets.Publication
import org.example.styleswebsite.components.widgets.SectionTitle
import org.example.styleswebsite.styles.CardShadowStyle
import org.example.styleswebsite.styles.ThemeMaxWidth
import org.example.styleswebsite.util.Lang
import org.example.styleswebsite.util.Res
import org.jetbrains.compose.web.css.AlignItems
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.cssRem
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Section

@Composable
fun Publications(ctx: PageContext) {
    Section(
        Modifier
            .padding(topBottom = 5.cssRem)
            .position(Position.Relative)
            .id("Publications")
            .backgroundImage(url(Res.Image.PUB_BACKGROUND))
            .backgroundSize(BackgroundSize.Cover)
            .backgroundPosition(BackgroundPosition.of(CSSPosition.Center))
            .backgroundAttachment(BackgroundAttachment.Fixed)
            .toAttrs()
    ) {
        Column(
            modifier = Modifier
                .position(Position.Absolute)
                .backgroundColor(color = Color.rgba(255,255,255,216))
                .top(0.px)
                .left(0.px)
                .right(0.px)
                .bottom(0.px)
        ) {}
        Column(
            ThemeMaxWidth.toModifier()
                .padding(leftRight = 1.5.cssRem)
                .margin(leftRight = autoLength)
                .fillMaxWidth()
                .alignItems(AlignItems.Center)
                .zIndex(10)
                .position(Position.Relative)
        ) {
            SectionTitle(
                title = if (ctx.route.path == "/de/") Lang.StringDE.PUBLICATIONS
                else Lang.StringEN.PUBLICATIONS,
                descr = if (ctx.route.path == "/de/") Lang.StringDE.PUB_DESCR
                else Lang.StringEN.PUB_DESCR,
            )
            Box(
                CardShadowStyle.toModifier()
                    .padding(all = 2.cssRem)
                    .backgroundColor(Res.Theme.LIGHT.color)
                    .borderRadius(16.px)
                    .maxWidth(56.cssRem)
            ) {
                Column(
                    Modifier
                        .fillMaxWidth()
                        .margin(all = 0.px)
                        .padding(all = 0.px)
                ) {
                    Publication(
                        Lang.StringGLOBAL.PUB_1_TITLE,
                        Lang.StringGLOBAL.PUB_1_MED,
                        "",
                        0.0,
                        0.0
                    )
                    Publication(
                        Lang.StringGLOBAL.PUB_2_TITLE,
                        Lang.StringGLOBAL.PUB_2_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Publication(
                        Lang.StringGLOBAL.PUB_3_TITLE,
                        Lang.StringGLOBAL.PUB_3_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Publication(
                        Lang.StringGLOBAL.PUB_4_TITLE,
                        Lang.StringGLOBAL.PUB_4_MED,
                        "",
                        1.5,
                        1.5
                    )
                    Publication(
                        Lang.StringGLOBAL.PUB_5_TITLE,
                        Lang.StringGLOBAL.PUB_5_MED,
                        "",
                        1.5,
                        1.5
                    )
                }
            }
        }
    }
}
