@Composable
fun About(
    ctx: PageContext
) {
    Section(
        Modifier
            .padding(topBottom = 5.cssRem)
            .backgroundColor(Res.Theme.LIGHT_D.color)
            .id("About")
            .toAttrs()
    ) {
        Column(
            ThemeMaxWidth.toModifier()
                .margin(leftRight = autoLength)
                .padding(leftRight = 1.5.cssRem)
                .alignItems(AlignItems.Center)
                .fillMaxWidth()
        ) {
            SectionTitle(
                title = if (ctx.route.path == "/de/") Lang.StringDE.ABOUT
                else Lang.StringEN.ABOUT,
                descr = if (ctx.route.path == "/de/") Lang.StringDE.ABOUT_DESCR
                else Lang.StringEN.ABOUT_DESCR,
            )
            Box(
                CardShadowStyle.toModifier()
                    .padding(all = 2.cssRem)
                    .backgroundColor(Res.Theme.LIGHT.color)
                    .borderRadius(16.px)
                    .maxWidth(56.cssRem)
                    .overflow(Overflow.Hidden)
            ) {
                Column(
                    AboutStyle.toModifier()
                        .gap(2.5.cssRem)
                ) {
                    Column(
                        PortraitStyle.toModifier()
                            .justifyContent(JustifyContent.Center)
                    ) {
                        Box(
                            Modifier
                                .borderRadius(9999.px)
                                .width(15.cssRem)
                                .height(15.cssRem)
                                .overflow(Overflow.Hidden)
                        ) {
                            Image(
                                src = Res.Image.PORTRAIT,
                                modifier = Modifier
                                    .objectFit(ObjectFit.Cover)
                                    .width(100.percent)
                                    .height(100.percent)
                                    .maxWidth(100.percent)
                                    .verticalAlign(VerticalAlign.Middle)
                            )
                        }
                    }
                    Column(
                        AboutTextStyle.toModifier()
                    ) {
                        SpanText(
                            Res.String.NAME,
                            Modifier
                                .fontFamily(Res.String.PLAYFAIRDISPLAY_REGULAR)
                                .color(Res.Theme.FONT_DARK.color)
                                .fontWeight(600)
                                .fontSize(1.5.cssRem)
                                .lineHeight(2.cssRem)
                                .margin(bottom = 1.cssRem)
                        )
                        SpanText(
                            if (ctx.route.path == "/de/") Lang.StringDE.ABOUT_ME_1 else Lang.StringEN.ABOUT_ME_1,
                            Modifier
                                .fontFamily(Res.String.INTERREG)
                                .color(Res.Theme.FONT_DARK.color)
                                .fontSize(1.cssRem)
                                .lineHeight(1.625.cssRem)
                                .margin(bottom = 1.cssRem)
                        )
                        SpanText(
                            if (ctx.route.path == "/de/") Lang.StringDE.ABOUT_ME_2 else Lang.StringEN.ABOUT_ME_2,
                            Modifier
                                .fontFamily(Res.String.INTERREG)
                                .color(Res.Theme.FONT_DARK.color)
                                .fontSize(1.cssRem)
                                .lineHeight(1.625.cssRem)
                                .margin(bottom = 1.cssRem)
                        )
                        SpanText(
                            if (ctx.route.path == "/de/") Lang.StringDE.ABOUT_ME_3 else Lang.StringEN.ABOUT_ME_3,
                            Modifier
                                .fontFamily(Res.String.INTERREG)
                                .color(Res.Theme.FONT_DARK.color)
                                .fontSize(1.cssRem)
                                .lineHeight(1.625.cssRem)
                                .margin(bottom = 1.cssRem)
                        )
                    }
                }
            }
        }
    }
}